  font-family: serif;
}

.wrapper {
  background: purple;
  background: blue;
  margin: 0 auto;
  max-width: 1200px;
  padding: 4rem;
  border: solid;
  border-radius: 25px;
}

header {
  height: 120px;
}

header h1 {
  margin-top: 1em;
}
